<template>
  <va-card :title="$t('dashboard.table.nonPaidBill')">

    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{(props.rowIndex + 1) + ((pageNumber  -1) *10) }}
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="customers" slot-scope="props">
        {{ props.rowData.customers.name === null ? '----':props.rowData.customers.name}}
      </template>
      <template slot="inv_PayType" slot-scope="props">
        {{ props.rowData.inv_PayType.name=== null ? '----':props.rowData.inv_PayType.name}}
      </template>

      <template slot="inv_dueDate" slot-scope="props">
        {{ formatDate(props.rowData.inv_dueDate)}}
      </template>
      <template slot="actions" slot-scope="props">

        <va-button flat small color="info" @click="showViewModal(props.rowData)" class="ma-0">
          {{ $t('tables.view') }}
        </va-button>
      </template>
    </va-data-table>
    <va-modal v-model="viewModal" no-outside-dismiss
      size="large"
      :title="$t('generic.viewItemTitle')"
      hide-default-actions >
      <va-data-table
        :fields="subFields"
        :data="currentobject.inv_Subs"
        api-mode
        hoverable
      >
        <template slot="marker" slot-scope="props">
          {{(props.rowIndex + 1) + ((pageNumber  -1) *10) }}
        </template>

        <template slot="products" slot-scope="props">
          {{ props.rowData.products.name === null ? '----':props.rowData.products.name}}
        </template>
        <template slot="units" slot-scope="props">
          {{ props.rowData.units.name=== null ? '----':props.rowData.units.name}}
        </template>
        <template slot="stock" slot-scope="props">
          {{ props.rowData.stock.name=== null ? '----':props.rowData.stock.name}}
        </template>
      </va-data-table>
      <div class=" row d-flex justify--center mt-3">
        <va-button type="button" color="gray" flat @click=" cancleVeiwfun()">
          {{ $t('modal.cancel') }}
        </va-button>
      </div>
    </va-modal>
  </va-card>
</template>

<script>
import GetNonPaidBillsService from '../../services/Reports/GetNonPaidBillsService.js'
export default {
  data () {
    return {

      stopBtn: false,
      is_view: false,
      perPage: 6,
      totalPages: 0,
      dataTable: [],
      viewModal: false,
      loading: true,
      opjectBeforeEdit: {},
      pageNumber: 1,
      currentobject: {

      },
    }
  },
  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'inv_id',
        title: this.$t('tables.headings.inv_id'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|inv_id',
      },
      {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|date',
      },
      {
        name: '__slot:customers',
        title: this.$t('tables.headings.customersName'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|customers',
      },
      {
        name: '__slot:inv_PayType',
        title: this.$t('tables.headings.inv_PayType'),
      },
      {
        name: 'total_Price',
        title: this.$t('tables.headings.total_Price'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|total_Price',
      },
      {
        name: 'paid',
        title: this.$t('tables.headings.paid'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|paid',
      },
      {
        name: '__slot:inv_dueDate',
        title: this.$t('tables.headings.inv_dueDate'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|inv_dueDate',
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
    subFields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: '__slot:products',
        title: this.$t('tables.headings.name'),
      },
      {
        name: '__slot:units',
        title: this.$t('tables.headings.units'),
      },
      {
        name: 'quantity',
        title: this.$t('tables.headings.quantity'),
      },
      {
        name: '__slot:stock',
        title: this.$t('tables.headings.stock'),
      },
      {
        name: 'price',
        title: this.$t('tables.headings.total_Price'),
      },
      {
        name: 'tax',
        title: this.$t('tables.headings.tax'),
      },
      {
        name: 'disscountPercentage',
        title: this.$t('tables.headings.disscountPercentage'),
      }]
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
    readItems (page = 1, orderBy = null) {
      this.pageNumber = page
      this.loading = true
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        OrderBy: orderBy,
        Fk_CompanyActivity: 1,
      }
      GetNonPaidBillsService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    sortCallBack (obj) {
      console.log('ads')
      this.readItems(1, (obj[0].toString().charAt(0).toUpperCase() + obj[0].toString().slice(1)))
    },
    showViewModal (cObj) {
      this.viewModal = true
      this.currentobject = cObj
    },
    cancleVeiwfun () {
      this.viewModal = false
      this.currentobject = {}
    },
  },
}
</script>

